// Buttons variable
@import "../../base/variables/font";
@import "../../theme/variable";

$btn-min-width: 4rem !default;
$btn-padding-x: 1rem !default;
$btn-padding-y: .625rem !default;
$btn-border-radius: 2px !default;
$btn-line-height: 1 !default;
$btn-font-size: $font-size-base !default;
$btn-font-weight: $font-weight-normal !default;

//  button ripple 
$btn-unfilled-ripple-opacity: .16;
$btn-filled-ripple-opacity: .32;

// btn 
$btn-padding-x-lg: 1.25rem !default;
$btn-padding-y-lg: 1rem !default;
$btn-border-radius-lg: 4px !default;
$btn-line-height-lg: 1 !default;

$btn-padding-x-sm: .75rem !default;
$btn-padding-y-sm: .5rem !default;
$btn-border-radius-sm: 2px !default;
$btn-line-height-sm: 1.25 !default;

// disabled 
$btn-bg-disabled: map-get($greys, 300) !default;

// 
$enable-custom-theme-for-btn: false !default;
