// 
@import "../../base/variables/font";
@import "../../theme/mixin";
@import "../../animation/mixin";
@import "../../base/mixins/gradients";
@import "./variable";
@import "./function";

// 
.progress {
  box-sizing:       border-box;
  display:          flex;
  width:            100%;
  height:           $progress-height;
  border-radius:    $progress-border-radius;
  overflow:         hidden;
  background-color: $progress-bg;

  &-inner {
    position: relative;
    display:         flex;
    flex-direction:  column; // 
    align-items:     center;
    justify-content: center;
    @include theme-prop(color, secondary-on-dark);
    @include theme-prop(background-color, secondary-on-light);
    transition:      progress-transition(width);
  }

  &-inner-text {
    position:  absolute;
    top:       -20px;
    font-size: $font-size-xs;
    color:     #fff;
  }

  // 
  &-inner-striped {
    @include gradient-striped();
    background-size: $progress-height $progress-height;
  }

  // 
  &-inner-animated {
    animation: progress-inner-stripes $progress-bar-animation-timing;
  }
}

@keyframes progress-inner-stripes {
  from { background-position: $progress-height 0; }
  to { background-position: 0 0; }
}

// 
.progress-bar {
  position:         relative;
  display:          flex;
  justify-content:  space-between;
  width:            100%;
  height:           3px;
  background-color: #eee;

  &-dot {
    position:         relative;
    top:              -.4rem;
    box-sizing:       border-box;
    width:            .875rem;
    height:           .875rem;
    border:           .25rem solid #eee;
    border-radius:    100%;
    background-color: #fff;

    &.active {
      border-color:     #f5d0d1;
      background-color: #cf121b;
    }
  }
}
